import { Button, Form, FormInstance, Input } from "antd";
import React from "react";
import { AppHeading, FileInputUploader } from "..";
import { AppContext, setCurrentPage, setInquiry } from "../../context";
import { FileInput, Inquiry, UIScreen } from "../../types";
import { FormValidationMessage } from "../FormValidationMessage";

export const InquiryForm = () => {
    const { state, dispatch, sendInquiry } = React.useContext(AppContext);
    const formRef = React.createRef<FormInstance>();

    const handleSubmit = (values: any) => {
        if (state.employeeInfo) {
            const { emailWork, contactNo, emailHome } = state.employeeInfo;
            const currentEmail = emailWork || emailHome;
            const inquiry: Inquiry = {
                newEmailHome: values.email,
                hasChangeEmail: currentEmail !== values.email,
                contactNo: values.phone,
                hasChangeContactNo: contactNo !== values.phone,
                messageDetails: values.messageDetails,
                attachments: values.attachment
                    ? [
                          {
                              ...values.attachment,
                          },
                      ]
                    : [],
                primaryCategoryId: state.selectedCategory?.id || "",
                subCategoryId: state.selectedSubCategory?.id || "",
                updateRecord: false,
            };
            //  || inquiry.hasChangeContactNo // Temporarily disabled
            if (inquiry.hasChangeEmail) {
                dispatch(setInquiry(inquiry));
                dispatch(setCurrentPage(UIScreen.UPDATE_FORM));
            } else {
                // Send Inqiury API here
                sendInquiry(inquiry);
                // dispatch(setCurrentPage(UIScreen.THANK_YOU));
            }
        }
    };

    React.useEffect(() => {
        const initVals = {
            employeeNo: state.employeeInfo?.employeeNo,
            categoryPrimary: state.selectedCategory?.id,
            primaryName: state.selectedCategory?.name,
            categorySecondary: state.selectedSubCategory?.id,
            secondaryName: state.selectedSubCategory?.name,
            messageDetails: state.inquiry?.messageDetails,
            email:
                state.inquiry?.newEmailHome ||
                state.employeeInfo?.emailWork ||
                state.employeeInfo?.emailHome ||
                "",
            phone: state.inquiry?.contactNo || state.employeeInfo?.contactNo,
            attachment: state.inquiry?.attachments.length
                ? {
                      ...state.inquiry?.attachments[0],
                  }
                : undefined,
        };
        console.log(initVals);
        formRef.current?.setFieldsValue(initVals);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        <div className="ss-eoa-form-screen ss-eoa-inquiry-screen">
            {state.requestError && (
                <FormValidationMessage message="There was an issue updating your details. Please try again." />
            )}
            <AppHeading
                subTitle={`I need help with ${state.selectedCategory?.name.toLowerCase()}`}
            />
            {state.selectedSubCategory && (
                <p>
                    I want to enquire about my{" "}
                    {state.selectedSubCategory?.name.toLowerCase()}
                </p>
            )}
            <Form
                ref={formRef}
                layout="vertical"
                requiredMark={false}
                onFinish={handleSubmit}
                scrollToFirstError
            >
                <Form.Item name="employeeNo" noStyle>
                    <Input type="hidden" />
                </Form.Item>
                <Form.Item name="categoryPrimary" noStyle>
                    <Input type="hidden" />
                </Form.Item>
                <Form.Item name="primaryName" noStyle>
                    <Input type="hidden" />
                </Form.Item>
                <Form.Item name="categorySecondary" noStyle>
                    <Input type="hidden" />
                </Form.Item>
                <Form.Item name="secondaryName" noStyle>
                    <Input type="hidden" />
                </Form.Item>

                <Form.Item
                    name="messageDetails"
                    label="Details"
                    rules={[
                        {
                            required: true,
                            message: (
                                <FormValidationMessage message="This is a required field." />
                            ),
                        },
                    ]}
                >
                    <Input.TextArea autoSize={true} />
                </Form.Item>

                <Form.Item
                    name="attachment"
                    label="Attachment (Optional)"
                    rules={[
                        {
                            validator: (_, value: FileInput) => {
                                if (value) {
                                    const filesize =
                                        value.filesize / 1024 / 1024;
                                    const isAcceptedFile =
                                        value.mimetype === "image/jpeg" ||
                                        value.mimetype === "image/png" ||
                                        value.mimetype === "image/jpg";
                                    if (filesize > 20) {
                                        return Promise.reject(
                                            <FormValidationMessage message="You can only attach document that is less that 20mb in size." />
                                        );
                                    } else if (!isAcceptedFile) {
                                        return Promise.reject(
                                            <FormValidationMessage message="You can only attach JPG, JPEG and PNG files." />
                                        );
                                    }
                                }
                                return Promise.resolve();
                            },
                        },
                    ]}
                >
                    <FileInputUploader />
                </Form.Item>

                <Form.Item
                    name="email"
                    label="Personal Email Address"
                    rules={[
                        {
                            required: true,
                            message: (
                                <FormValidationMessage message="Please add your email address" />
                            ),
                        },
                        {
                            type: "email",
                            message: (
                                <FormValidationMessage message="Email address is invalid" />
                            ),
                        },
                    ]}
                >
                    <Input readOnly={!!state.employeeInfo?.emailWork} />
                </Form.Item>

                <Form.Item
                    name="phone"
                    label="Phone Number (Optional)"
                    rules={[
                        {
                            validator: (_, value) => {
                                if (value) {
                                    const re = /^[0-9)(+=._-\s]{6,}$/g;
                                    return re.test(value)
                                        ? Promise.resolve()
                                        : Promise.reject(
                                              <FormValidationMessage
                                                  message="The phone number format is invalid.
                                              "
                                              />
                                          );
                                }
                                return Promise.resolve();
                            },
                        },
                    ]}
                >
                    <Input />
                </Form.Item>

                <div className="ss-eoa-submit-btn">
                    <Button
                        htmlType="submit"
                        loading={state.sendingRequest}
                        type="primary"
                        block
                        shape="round"
                    >
                        <strong>Send Support Request</strong>
                    </Button>
                </div>
            </Form>
        </div>
    );
};
