import React, { LegacyRef } from "react";

import { AppContext, setShowMoreFotterMsg } from "../context";

export const AppFooterNote = () => {
    const { state, dispatch } = React.useContext(AppContext);
    const [isMsgLong, setIsMsgLong] = React.useState(false);
    const { showMoreFooterMsg } = state;
    const original: LegacyRef<HTMLDivElement> = React.createRef();
    const clone: LegacyRef<HTMLDivElement> = React.createRef();

    React.useEffect(() => {
        if (original.current && clone.current) {
            const origY = original.current.clientHeight;
            const cloneY = clone.current.clientHeight;
            setIsMsgLong(origY < cloneY);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        <div
            className={`ss-eoa-note${isMsgLong ? " with-long-note" : ""}${
                showMoreFooterMsg ? " expanded" : ""
            }`}
        >
            <div ref={original} className="ss-eoa-note-text">
                {state.employeeInfo?.footerMessage}
            </div>
            <div ref={clone} className="ss-eoa-note-clone">
                {state.employeeInfo?.footerMessage}
            </div>
            {isMsgLong && (
                <span
                    className="read-more"
                    onClick={() =>
                        dispatch(setShowMoreFotterMsg(!state.showMoreFooterMsg))
                    }
                >
                    Show {showMoreFooterMsg ? "less" : "more"}
                </span>
            )}
        </div>
    );
};
