import React from "react";

interface Props {
    title?: string;
    subTitle?: string;
}

export const AppHeading: React.FC<Props> = React.memo(({ title, subTitle }) => {
    return (
        <div className="ss-eoa-heading">
            {title && <h1>{title}</h1>}
            {subTitle && <h2>{subTitle}</h2>}
        </div>
    );
});
