import React from "react";
import { Button, Form, FormInstance, Input } from "antd";

import { AppHeading } from "..";
import { AppContext, setEmployeeInfo, setInquiry } from "../../context";
import { Employee } from "../../types";
import { FormValidationMessage } from "../FormValidationMessage";

export const UpdateContactForm = () => {
    const { state, dispatch, sendInquiry } = React.useContext(AppContext);
    const { inquiry, employeeInfo } = state;
    const formRef = React.createRef<FormInstance>();

    const handleSubmit = (values: any) => {
        if (state.inquiry) {
            const { updateRecord } = values;
            const _inquiry = {
                ...state.inquiry,
                newEmailHome: values.newEmail || "",
                contactNo: values.newPhone || state.inquiry.contactNo || "",
                updateRecord,
            };
            if (updateRecord) {
                const employeeInfo = {
                    ...state.employeeInfo,
                    contactNo: _inquiry.contactNo,
                    emailHome: _inquiry.newEmailHome,
                };
                dispatch(setEmployeeInfo(employeeInfo as Employee));
            }

            dispatch(setInquiry(_inquiry));
            // Send Inquiry API here
            sendInquiry(_inquiry);
        }
    };

    return (
        <div className="ss-eoa-form-screen ss-eoa-update-screen">
            {state.requestError && (
                <FormValidationMessage message="There was an issue updating your details. Please try again." />
            )}
            <AppHeading subTitle="Do you want to update your profile with these details?" />
            {state.selectedSubCategory && (
                <p>
                    The email address you entered does not match the one in your
                    profile.
                </p>
            )}

            <Form
                layout="vertical"
                ref={formRef}
                requiredMark={false}
                onFinish={handleSubmit}
                scrollToFirstError
                initialValues={{
                    updateRecord: true,
                    currentEmail: employeeInfo?.emailHome,
                    newEmail: inquiry?.newEmailHome,
                    currentPhone: employeeInfo?.contactNo,
                    newPhone: inquiry?.contactNo,
                }}
            >
                <Form.Item name="updateRecord" noStyle>
                    <Input type="hidden" />
                </Form.Item>
                {inquiry?.hasChangeEmail && (
                    <div className="ss-eoa-update-block">
                        {employeeInfo?.emailHome && (
                            <Form.Item
                                name="currentEmail"
                                label="Current Email Address"
                            >
                                <Input readOnly />
                            </Form.Item>
                        )}

                        <Form.Item
                            name="newEmail"
                            label="New Email Address"
                            rules={[
                                {
                                    required: true,
                                    message: (
                                        <FormValidationMessage message="Please add your email address" />
                                    ),
                                },
                                {
                                    type: "email",
                                    message: (
                                        <FormValidationMessage message="Email address is invalid" />
                                    ),
                                },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                    </div>
                )}

                {inquiry?.hasChangeContactNo && (
                    <div className="ss-eoa-update-block">
                        {employeeInfo?.contactNo && (
                            <Form.Item
                                name="currentPhone"
                                label="Current Phone Address"
                            >
                                <Input readOnly />
                            </Form.Item>
                        )}

                        <Form.Item
                            name="newPhone"
                            label="New Phone Number"
                            rules={[
                                {
                                    validator: (_, value) => {
                                        if (value) {
                                            const re = /^[0-9)(+=._-\s]{6,}$/g;
                                            return re.test(value)
                                                ? Promise.resolve()
                                                : Promise.reject(
                                                      <FormValidationMessage
                                                          message="The phone number format is invalid.
                                                      "
                                                      />
                                                  );
                                        }
                                        return Promise.resolve();
                                    },
                                },
                            ]}
                        >
                            <Input readOnly />
                        </Form.Item>
                    </div>
                )}

                <div className="ss-eoa-submit-btn">
                    <Button
                        htmlType="submit"
                        type="primary"
                        block
                        shape="round"
                    >
                        <strong>Yes, Update and Send</strong>
                    </Button>
                    <Button
                        className="ss-eoa-btn-gray"
                        block
                        shape="round"
                        onClick={() => {
                            let values = formRef.current?.getFieldsValue();
                            values = { ...values, updateRecord: false };
                            handleSubmit(values);
                        }}
                    >
                        No, Keep Current and Send
                    </Button>
                </div>
            </Form>
        </div>
    );
};
