import { Button } from "antd";
import Upload, { UploadChangeParam } from "antd/lib/upload";
import { UploadFile } from "antd/lib/upload/interface";
import React from "react";
import { EOAIcon } from ".";
import { FileInput } from "../types";
import { formatBytes, getBase64, uploadProps } from "../utils/fileUtils";

interface Props {
    value?: FileInput;
    disabled?: boolean;
    onChange?: (value?: FileInput) => void;
}

export const FileInputUploader: React.FC<Props> = ({
    value,
    disabled,
    onChange,
}) => {
    const [currentValue, setCurrentValue] = React.useState<FileInput>();
    const [imageState, setImageState] = React.useState<any>(value);

    React.useEffect(() => {
        if (value && currentValue?.filename !== value.filename) {
            setCurrentValue(value);
            setImageState(value);
        } else if (!value) {
            setImageState({
                base64: null,
                filename: "",
                isUploading: false,
            });
        }
        return function cleanup() {};
        // eslint-disable-next-line
    }, [value]);

    const triggerChange = (info: UploadChangeParam<UploadFile<any>>) => {
        setImageState({ ...imageState, isUploading: true });
        if (info.file.originFileObj) {
            getBase64(info.file.originFileObj, (base64: any) => {
                const { name, type, size } = info.file.originFileObj as any;
                setImageState({
                    base64,
                    filename: name,
                    isUploading: false,
                });
                const _val = {
                    filename: name,
                    mimetype: type,
                    base64,
                    filesize: size,
                };
                setCurrentValue(_val);
                onChange && onChange(_val);
            });
        }
        return false;
    };

    return (
        <Upload {...uploadProps} onChange={triggerChange} disabled={disabled}>
            {imageState && imageState.base64 ? (
                <span className="ss-eoa-attachment">
                    <EOAIcon asset="image" /> <span>{imageState.filename}</span>
                    <span>{formatBytes(value?.filesize || 0)}</span>
                    <Button
                        type="link"
                        className="ss-eoa-remove-file"
                        icon={<EOAIcon asset="x-button" />}
                        onClick={(e) => {
                            e.stopPropagation();
                            setCurrentValue(undefined);
                            onChange && onChange(undefined);
                        }}
                    />
                </span>
            ) : (
                <Button
                    className="ss-eoa-btn-attach"
                    type="dashed"
                    block
                    icon={<EOAIcon asset="upload" />}
                >
                    Upload file
                </Button>
            )}
        </Upload>
    );
};
