import { Button } from "antd";
import React from "react";
import { AppHeading } from "..";
import {
    AppContext,
    setCurrentPage,
    setSelectedSubCategory,
} from "../../context";
import { CommonField, UIScreen } from "../../types";

export const SubCategoryOptions = () => {
    const { state, dispatch } = React.useContext(AppContext);

    const handleSubCategoryClick = (subCategory: CommonField) => {
        dispatch(setCurrentPage(UIScreen.INQUIRY_FORM));
        dispatch(setSelectedSubCategory(subCategory));
    };

    return (
        <div className="ss-eoa-home-screen">
            <AppHeading
                title={`Hi ${state.employeeInfo?.firstNames},`}
                subTitle="How can we help you today?"
            />
            <p>I need help with {state.selectedCategory?.name.toLowerCase()}</p>
            <h3>I want to enquire about:</h3>
            <div className="ss-eoa-content-block ss-eoa-home-content">
                {state.selectedCategory &&
                    state.selectedCategory.subCategory.map((subCat) => (
                        <Button
                            key={`BTNCAT_${subCat.id}`}
                            shape="round"
                            block
                            onClick={() => handleSubCategoryClick(subCat)}
                        >
                            {subCat.name}
                        </Button>
                    ))}
            </div>
        </div>
    );
};
