import React, { createContext, useReducer } from "react";
import { setInquiry, SetRequestError, setSendingRequest } from ".";
import { postInquiry } from "../api";
import { Category, CommonField, Employee, Inquiry, UIScreen } from "../types";
import { Actions, ActionType, setCurrentPage } from "./actions";

interface IState {
    isAuth: Boolean;
    previousPage: UIScreen[];
    appLoading: boolean;
    employeeInfo?: Employee;
    categoryData?: Category[];
    inquiry?: Inquiry;
    showWidget: boolean;
    currentPage: UIScreen;
    selectedCategory?: Category;
    selectedSubCategory?: CommonField;
    sendingRequest: boolean;
    requestError: boolean;
    showMoreFooterMsg: boolean;
}

const initialState: IState = {
    isAuth: false,
    previousPage: [UIScreen.HOME],
    appLoading: false,
    sendingRequest: false,
    requestError: false,
    showWidget: false,
    currentPage: UIScreen.HOME,
    showMoreFooterMsg: false,
};

interface IAppContext {
    state: IState;
    dispatch: React.Dispatch<Actions>;
    sendInquiry: (inquiry: Inquiry) => void;
}

const AppContext = createContext<IAppContext>({
    state: initialState,
    dispatch: () => null,
    sendInquiry: () => null,
});

const { Provider } = AppContext;

const reducer = (state: IState, action: Actions) => {
    switch (action.type) {
        case ActionType.SetIsAuth:
            return {
                ...state,
                isAuth: action.payload,
            };

        case ActionType.SetAppLoading:
            return {
                ...state,
                appLoading: action.payload,
            };

        case ActionType.SetCategoryData:
            return {
                ...state,
                categoryData: action.payload,
            };

        case ActionType.SetEmployeeInfo:
            return {
                ...state,
                employeeInfo: action.payload,
            };

        case ActionType.SetInquiry:
            return {
                ...state,
                inquiry: action.payload,
            };

        case ActionType.SetShowWidget:
            return {
                ...state,
                showMoreFooterMsg: false,
                showWidget: action.payload,
            };

        case ActionType.SetPreviousPage:
            let _previousPage: UIScreen[] = [],
                currPage: UIScreen;
            if (action.payload) {
                _previousPage = action.payload;
                currPage = action.payload[0];
            } else {
                _previousPage = state.previousPage;
                _previousPage.pop();
                currPage = _previousPage[_previousPage.length - 1];
            }
            return {
                ...state,
                currentPage: currPage,
                showMoreFooterMsg: false,
                previousPage: _previousPage,
            };

        case ActionType.SetCurrentPage:
            const previousPage = [...state.previousPage, action.payload];
            return {
                ...state,
                previousPage,
                showMoreFooterMsg: false,
                currentPage: action.payload,
            };

        case ActionType.SetSelectedCategory:
            return {
                ...state,
                selectedCategory: action.payload,
            };

        case ActionType.SetSelectedSubCategory:
            return {
                ...state,
                selectedSubCategory: action.payload,
            };

        case ActionType.SetSendingRequest:
            return {
                ...state,
                sendingRequest: action.payload,
            };

        case ActionType.SetRequestError:
            return {
                ...state,
                requestError: action.payload,
            };

        case ActionType.SetShowMoreFotterMsg:
            return {
                ...state,
                showMoreFooterMsg: action.payload,
            };

        default:
            return state;
    }
};

const AppProvider = ({ children }: { children: JSX.Element }) => {
    const [state, dispatch] = useReducer(reducer, initialState);
    const sendInquiry = (inquiry: Inquiry) => {
        dispatch(setInquiry(inquiry));
        const _inquiry = {
            ...inquiry,
            attachments: inquiry.attachments
                .filter((f) => f.base64)
                .map((f) => {
                    const _s = f.base64.split(","),
                        fileAttachment = _s.length ? _s[1] : "";
                    return {
                        fileName: f.filename,
                        fileAttachment,
                    };
                }),
        };
        delete _inquiry.hasChangeContactNo;
        delete _inquiry.hasChangeEmail;

        dispatch(setSendingRequest(true));
        dispatch(SetRequestError(false));
        postInquiry(_inquiry)
            .then((res) => {
                dispatch(setCurrentPage(UIScreen.THANK_YOU));
                dispatch(setInquiry(undefined));
            })
            .catch(() => {
                dispatch(SetRequestError(true));
            })
            .finally(() => dispatch(setSendingRequest(false)));
    };

    return (
        <Provider value={{ state, dispatch, sendInquiry }}>{children}</Provider>
    );
};

export { AppContext, AppProvider };
