import React from "react";
import { Button } from "antd";
import { AppHeading } from "..";
import {
    AppContext,
    setCurrentPage,
    setSelectedCategory,
    setSelectedSubCategory,
} from "../../context";
import { Category, UIScreen } from "../../types";

export const Home = () => {
    const { state, dispatch } = React.useContext(AppContext);

    const handleCategoryClick = (category: Category) => {
        dispatch(setSelectedCategory(category));
        dispatch(setSelectedSubCategory(undefined));
        if (category.subCategory.length) {
            dispatch(setCurrentPage(UIScreen.SUBCATEGORY));
        } else {
            dispatch(setCurrentPage(UIScreen.INQUIRY_FORM));
        }
    };

    return (
        <div className="ss-eoa-home-screen">
            <AppHeading
                title={`Hi ${state.employeeInfo?.firstNames},`}
                subTitle="How can we help you today?"
            />
            <p>I need help with:</p>
            <div className="ss-eoa-content-block ss-eoa-home-content">
                {state.categoryData &&
                    state.categoryData.map((cat) => (
                        <Button
                            key={`BTNCAT_${cat.id}`}
                            shape="round"
                            block
                            onClick={() => handleCategoryClick(cat)}
                        >
                            {cat.name}
                        </Button>
                    ))}
            </div>
            <p>I want to access guides and documentation:</p>
            <div className="ss-eoa-content-block ss-eoa-home-content">
                {/* <Button shape="round" block>
                    View FAQ
                </Button> */}
                <Button
                    shape="round"
                    block
                    onClick={() =>
                        window.open(
                            "https://dashboard.affinitylogon.com/Help/Index",
                            "_blank"
                        )
                    }
                >
                    View Help Centre
                </Button>
            </div>
        </div>
    );
};
