import { LoadingOutlined, PlusOutlined } from "@ant-design/icons";
import { message } from "antd";
import { UploadListType } from "antd/lib/upload/interface";
import React from "react";

export const normFile = (e: any) => {
    if (Array.isArray(e)) {
        return e;
    }
    return e && e.fileList;
};

export const beforeUpload = (file: any) => {
    const isAcceptedFile =
        file.type === "image/jpeg" ||
        file.type === "image/png" ||
        file.type === "image/jpg";

    if (!isAcceptedFile) {
        message.error("You can only upload JPG/PNG file!");
    }
    const isLt20M = file.size / 1024 / 1024 < 20;
    if (!isLt20M) {
        message.error("File must equal or less than 20MB!");
    }
    return isAcceptedFile && isLt20M;
};

export const uploadProps = {
    name: "file",
    multiple: false,
    accept: ".png, .jpg, .jpeg",
    showUploadList: false,
    maxCount: 1,
    listType: "picture-card" as UploadListType | undefined,
    customRequest: () => false,
};

export const getBase64 = (img: any, callback: any) => {
    const reader = new FileReader();
    reader.addEventListener("load", () => callback(reader.result));
    reader.readAsDataURL(img);
};

interface UBProps {
    loading: boolean;
    btnLabel: React.ReactNode | string;
}

export const UploadButton: React.FC<UBProps> = ({ loading, btnLabel }) => (
    <div>
        {loading ? <LoadingOutlined /> : <PlusOutlined />}
        <div style={{ marginTop: 8, fontSize: 10 }}>
            {loading ? "Uploading image..." : btnLabel}
        </div>
    </div>
);

export function formatBytes(bytes: number, decimals = 2) {
    if (bytes === 0) return "0 Bytes";

    const k = 1024;
    const dm = decimals < 0 ? 0 : decimals;
    const sizes = ["Bytes", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"];

    const i = Math.floor(Math.log(bytes) / Math.log(k));

    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + " " + sizes[i];
}
