import React from "react";
import { EOAIcon } from ".";

interface Props {
    message: string;
}

export const FormValidationMessage: React.FC<Props> = ({ message }) => {
    return (
        <div className="ss-eoa-validate-message">
            <EOAIcon asset="validation-error" />
            <span>{message}</span>
        </div>
    );
};
