import { Button } from "antd";
import React from "react";
import { fetchAppData } from "./api";
import { AppContent, AppToolTip, EOAIcon } from "./components";
import { Category, UIScreen } from "./types";
import {
    AppContext,
    setCategoryData,
    setEmployeeInfo,
    setPreviousPage,
    setShowWidget,
} from "./context";
import "./App.less";

const resource = fetchAppData();

function App() {
    const { state, dispatch } = React.useContext(AppContext);
    const category = resource.category.read();
    const employee = resource.employee.read();
    const [stopApp, setStopApp] = React.useState(false);

    React.useEffect(() => {
        dispatch(setEmployeeInfo(employee));
        let cats = category.map((cat: Category) => ({
            ...cat,
            subCategory: cat.subCategory || [],
        }));

        dispatch(setCategoryData(cats));
        if (employee) {
            setStopApp(!employee.internalEmailAddress);
        }
    }, [dispatch, category, employee]);

    if (stopApp) {
        return null;
    }

    return (
        <div
            className={`ss-eoa-wisdget ${
                state.showWidget ? "ss-eoa-shown" : "ss-eoa-hidden"
            }`}
        >
            <AppContent />
            <Button
                size="large"
                className={`ss-eoa-btn-qmark ${
                    [UIScreen.UPDATE_FORM, UIScreen.THANK_YOU].includes(
                        state.currentPage
                    )
                        ? " ss-eoa-dark-btn"
                        : ""
                }`}
                onClick={() => {
                    if (
                        state.currentPage === UIScreen.THANK_YOU &&
                        state.showWidget
                    ) {
                        dispatch(setPreviousPage([UIScreen.HOME]));
                    }
                    dispatch(setShowWidget(!state.showWidget));
                }}
                icon={<EOAIcon asset="q-mark" />}
                shape="circle"
            />
            <AppToolTip />
        </div>
    );
}

export default App;
