import "react-app-polyfill/ie11";
import "core-js/stable";

import "core-js/es/map";
import "core-js/es/set";

import React from "react";
import ReactDOM from "react-dom";
import { ConfigProvider } from "antd";
import App from "./App";
import { AppProvider } from "./context";

(function (d) {
    if (!d.getElementById("EOA-APP")) {
        const head = document.head || document.getElementsByTagName("head")[0];
        const f = d.createElement("link");
        f.setAttribute(
            "href",
            "https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap"
        );
        f.setAttribute("rel", "stylesheet");

        const fg = d.createElement("link");
        fg.setAttribute("href", "https://fonts.googleapis.com");
        fg.setAttribute("rel", "preconnect");

        const fgs = d.createElement("link");
        fgs.setAttribute("href", "https://fonts.gstatic.com");
        fgs.setAttribute("rel", "preconnect");
        fgs.setAttribute("crossorigin", "true");

        head.appendChild(fg);
        head.appendChild(fgs);
        head.appendChild(f);

        const EOAWrap = d.createElement("div");
        EOAWrap.id = "EOA-APP";
        d.body.appendChild(EOAWrap);

        ReactDOM.render(
            <ConfigProvider prefixCls="ss-eoa">
                <React.StrictMode>
                    <AppProvider>
                        <React.Suspense fallback={""}>
                            <App />
                        </React.Suspense>
                    </AppProvider>
                </React.StrictMode>
            </ConfigProvider>,
            EOAWrap
        );
    }
})(document);
