import axios from "axios";

axios.interceptors.request.use((config) => {
    let authHeader: any = {
        // "Cache-Control": "no-cache",
    };
    // const token = localStorage.getItem("OMMSAuthToken");
    // if (token) {
    //     authHeader.Authorization = `Bearer ${token}`;
    // }
    config.withCredentials = true;
    config.headers = {
        ...config.headers,
        ...authHeader,
        "content-type": "application/json; charset=utf-8",
    };
    config.baseURL = process.env.REACT_APP_API_URL;

    return config;
});

axios.interceptors.response.use(
    (res) => {
        switch (res.status) {
            case 401: // Unauthorize
                break;
        }
        // Return data API Content
        return res;
    },
    (errors) => {
        return Promise.reject(errors);
    }
);

export function fetchAppData() {
    let category = getCategoryList();
    let employee = getEmployee();
    return {
        category: wrapPromise(category),
        employee: wrapPromise(employee),
    };
}

export const getCategoryList = () => {
    return axios.get("/api/Category").then((res) => res.data);
};

export const getEmployee = () => {
    return axios.get("/api/Employee").then((res) => res.data);
};

export const postInquiry = (inquiry: any) => {
    return axios.post("/api/SendInquiry", inquiry).then((res) => res.data);
};

export const wrapPromise = (promise: Promise<any>) => {
    let status = "pending";
    let result: any;
    let suspender = promise.then(
        (res) => {
            status = "success";
            result = res;
        },
        (err) => {
            status = "error";
            result = err;
        }
    );

    return {
        read() {
            if (status === "pending") {
                throw suspender;
            } else if (status === "error") {
                throw result;
            }
            return result;
        },
    };
};
