import React from "react";
import { AppFooterNote, AppLoader, AppNavigation } from ".";
import { AppContext } from "../context";
import { UIScreen } from "../types";
import { Home, SubCategoryOptions, InquiryForm, Success } from "./Screens";
import { UpdateContactForm } from "./Screens/UpdateContactForm";

export const AppContent = () => {
    const { state } = React.useContext(AppContext);
    const { showMoreFooterMsg } = state;

    const showFooterMsg =
        [UIScreen.INQUIRY_FORM, UIScreen.UPDATE_FORM].includes(
            state.currentPage
        ) && state.employeeInfo?.footerMessage;

    if (!state.showWidget) {
        return null;
    }

    const content = () => {
        switch (state.currentPage) {
            case UIScreen.SUBCATEGORY:
                return <SubCategoryOptions />;
            case UIScreen.INQUIRY_FORM:
                return <InquiryForm />;
            case UIScreen.UPDATE_FORM:
                return <UpdateContactForm />;
            case UIScreen.THANK_YOU:
                return <Success />;
            default:
                return <Home />;
        }
    };

    return (
        <div
            className={`ss-eoa-wrapper ${
                showMoreFooterMsg ? "footer-note-open" : ""
            }`}
        >
            <AppLoader />
            <AppNavigation />
            <div className={`ss-eoa-content ${state.currentPage || "Index"}`}>
                {content()}
            </div>
            {showFooterMsg && <AppFooterNote />}
        </div>
    );
};
