import { Button } from "antd";
import React from "react";
import { EOAIcon } from ".";
import { AppContext, setPreviousPage, setShowWidget } from "../context";
import { UIScreen } from "../types";

export const AppNavigation = () => {
    const { state, dispatch } = React.useContext(AppContext);
    return (
        <div className="ss-eoa-nav">
            {![UIScreen.HOME, UIScreen.THANK_YOU].includes(
                state.currentPage
            ) && (
                <Button
                    type="link"
                    className="ss-eoa-back-btn"
                    icon={<EOAIcon asset="back-arrow" />}
                    onClick={() => dispatch(setPreviousPage())}
                />
            )}
            <Button
                type="link"
                className="ss-eoa-close-btn"
                icon={<EOAIcon asset="close" />}
                onClick={() => {
                    if (state.currentPage === UIScreen.THANK_YOU) {
                        dispatch(setPreviousPage([UIScreen.HOME]));
                    }
                    dispatch(setShowWidget(false));
                }}
            />
        </div>
    );
};
