import { Category, CommonField, Employee, Inquiry, UIScreen } from "../types";

export enum ActionType {
    SetEmployeeInfo = "SET_EMPLOYEE_INFO",
    SetCategoryData = "SET_CATEGORY_DATA",
    SetInquiry = "SET_INQUIRY",
    SetPreviousPage = "SET_PREVIOUS_PAGE",
    SetIsAuth = "SET_IS_AUTHENTICATED",
    SetAppLoading = "SET_APP_LOADING",
    SetShowWidget = "SET_SHOW_WIDGET",
    SetCurrentPage = "SET_CURRENT_PAGE",
    SetSelectedCategory = "SET_SELECTED_CATEGORY",
    SetSelectedSubCategory = "SET_SELECTED_SUB_CATEGORY",
    SetSendingRequest = "SET_SENDING_REQUEST",
    SetRequestError = "SET_SENDING_ERROR",
    SetShowMoreFotterMsg = "SET_SHOW_MORE_FOOTER_MESSAGE",
}

interface ISetEmployeeInfo {
    type: ActionType.SetEmployeeInfo;
    payload: Employee;
}

interface ISetCategoryData {
    type: ActionType.SetCategoryData;
    payload: Category[];
}

interface ISetIsAuth {
    type: ActionType.SetIsAuth;
    payload: boolean;
}

interface ISetAppLoading {
    type: ActionType.SetAppLoading;
    payload: boolean;
}

interface ISetPreviousPage {
    type: ActionType.SetPreviousPage;
    payload?: UIScreen[];
}

interface ISetInquiry {
    type: ActionType.SetInquiry;
    payload?: Inquiry;
}

interface ISetShowWidget {
    type: ActionType.SetShowWidget;
    payload: boolean;
}

interface ISetCurrentPage {
    type: ActionType.SetCurrentPage;
    payload: UIScreen;
}

interface ISetSelectedCategory {
    type: ActionType.SetSelectedCategory;
    payload: Category;
}

interface ISetSelectedSubCategory {
    type: ActionType.SetSelectedSubCategory;
    payload?: CommonField;
}

interface ISetSendingRequest {
    type: ActionType.SetSendingRequest;
    payload: boolean;
}

interface ISetRequestError {
    type: ActionType.SetRequestError;
    payload: boolean;
}

interface ISetShowMoreFotterMsg {
    type: ActionType.SetShowMoreFotterMsg;
    payload: boolean;
}

export type Actions =
    | ISetIsAuth
    | ISetEmployeeInfo
    | ISetAppLoading
    | ISetCategoryData
    | ISetPreviousPage
    | ISetInquiry
    | ISetShowWidget
    | ISetCurrentPage
    | ISetSelectedCategory
    | ISetSelectedSubCategory
    | ISetSendingRequest
    | ISetRequestError
    | ISetShowMoreFotterMsg;

export const setIsAuth = (response: boolean): ISetIsAuth => ({
    type: ActionType.SetIsAuth,
    payload: response,
});

export const setAppLoading = (response: boolean): ISetAppLoading => ({
    type: ActionType.SetAppLoading,
    payload: response,
});

export const setEmployeeInfo = (response: Employee): ISetEmployeeInfo => ({
    type: ActionType.SetEmployeeInfo,
    payload: response,
});

export const setCategoryData = (response: Category[]): ISetCategoryData => ({
    type: ActionType.SetCategoryData,
    payload: response,
});

export const setPreviousPage = (response?: UIScreen[]): ISetPreviousPage => ({
    type: ActionType.SetPreviousPage,
    payload: response,
});

export const setInquiry = (response?: Inquiry): ISetInquiry => ({
    type: ActionType.SetInquiry,
    payload: response,
});

export const setShowWidget = (response: boolean): ISetShowWidget => ({
    type: ActionType.SetShowWidget,
    payload: response,
});

export const setCurrentPage = (response: UIScreen): ISetCurrentPage => ({
    type: ActionType.SetCurrentPage,
    payload: response,
});

export const setSelectedCategory = (
    response: Category
): ISetSelectedCategory => ({
    type: ActionType.SetSelectedCategory,
    payload: response,
});

export const setSelectedSubCategory = (
    response?: CommonField
): ISetSelectedSubCategory => ({
    type: ActionType.SetSelectedSubCategory,
    payload: response,
});

export const setSendingRequest = (response: boolean): ISetSendingRequest => ({
    type: ActionType.SetSendingRequest,
    payload: response,
});

export const SetRequestError = (response: boolean): ISetRequestError => ({
    type: ActionType.SetRequestError,
    payload: response,
});

export const setShowMoreFotterMsg = (
    response: boolean
): ISetShowMoreFotterMsg => ({
    type: ActionType.SetShowMoreFotterMsg,
    payload: response,
});
